@media only screen and (max-width: 450px) {
    .text-sm-small {
        font-size: 80% !important;
        font-weight: 400 !important;
    }
    .text-sm-lg { font-size: 2rem !important; }
    .text-sm-md { font-size: 1rem !important; }

    .w-sm-100 { width: 100% !important; }
    .w-sm-90 { width: 90% !important; }
    .font-size-sm__3rem { font-size: 3rem !important; }
    
    .round__profile__picture-sm {
        width: 50px;
        height: 50px;
    }

    .translatey-sm__-25px { transform: translateY(-25px); }


}
